body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
li {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
  &:visited{
    color: inherit;
  }
}

button {
  background-color: transparent;
  border: none;
  padding: 0;
  text-transform: none;
  cursor: pointer;
}

img {
  max-width: 100%;
}
